___CSS_LOADER_EXPORT___.push([module.id, `/* CSS reset */
/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

.result-container {
  margin: 12px auto 4px;
  height: 30px;
  background-color: #fffbdb;
}

.result {
  text-align: center;
  font-size: 24px;
}

.section-container {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: space-around;
  padding: 8px 24px;
}

.row {
  display: flex;
}

.cell {
  background-color: #2b2d42;
  width: 50px;
  height: 50px;
  margin: 1px;
}

.occupied {
  background-color: #95948f;
}

.missed {
  background-color: #8d99ae;
}

.interact:hover {
  background-color: #fffbdb;
  cursor: pointer;
}

.hit {
  background-color: #e5625e;
}

.reposition {
  border: 1px solid black;
  border-radius: 4px;
  padding: 4px 8px;
  display: block;
  margin: 8px auto;
  cursor: pointer;
}

.reposition:hover {
  border: 2px solid black;
}
